import React from "react";
import {Link} from "react-router-dom";
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({year, rating, title, summary, poster, genres}){
	return (
		<Link to={{
			pathname: "/movie-detail",
			state: {
				year,
				title,
				poster,
				summary,
				genres
			}
		 }}
		>
			<div className="movie">
				<div className="upper">
					<div className="movie__poster">
						<img src={poster} alt={title} />
					</div>
					<div className="movie__data">
						<div className="movie__title-year">
							<div><h3 className="movie__title">{title}</h3></div>
							<div><h5 className="movie__year">{year}</h5></div>
						</div>
						<ul className="genres">{genres.map((genre, index) =>(<li key={index} className="genres__genre">{genre}</li>))}</ul>
						<div className="rating-div">
							<span>{rating}</span>
						</div>
						<div className="movie_summary-div">
							<span className="movie__summary">
								{summary.slice(0, 140)}...
								<svg className="read-more" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="#adadad" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round"><circle cx="12" cy="12" r="1"></circle><circle cx="19" cy="12" r="1"></circle><circle cx="5" cy="12" r="1"></circle></svg>
							</span>
						</div>
					</div>
					<div className="more">
						<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="#bbb5c1" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round"><circle cx="12" cy="12" r="1"></circle><circle cx="12" cy="5" r="1"></circle><circle cx="12" cy="19" r="1"></circle></svg>
					</div>
				</div>
				<div className="below">
					<div className="recommendation">
						<span className="friends">
							<img className="friend-pf" alt="friend" src="https://asianwiki.com/images/b/bd/Jungkook_(BTS)-P1.jpg"/>
							<img className="friend-pf" alt="friend" src="https://upload.wikimedia.org/wikipedia/commons/0/0e/190417_V_at_the_Map_of_the_Soul_Persona_press_conference.jpg"/>
							<img className="friend-pf" alt="friend" src="https://www.thefamouspeople.com/profiles/images/j-hope-5.jpg"/>
						</span>
						<span>
							friend and others watching this
						</span>
					</div>
					<div className="thums-up">
						<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="#93ade9" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
							<path d="M14 9V5a3 3 0 0 0-3-3l-4 9v11h11.28a2 2 0 0 0 2-1.7l1.38-9a2 2 0 0 0-2-2.3zM7 22H4a2 2 0 0 1-2-2v-7a2 2 0 0 1 2-2h3"></path>
						</svg>
					</div>
				</div>
			</div>
		</Link>
	);
}



Movie.propTypes ={
	id: PropTypes.number.isRequired,
	year: PropTypes.number.isRequired,
	rating: PropTypes.number.isRequired,
	title: PropTypes.string.isRequired,
	summary: PropTypes.string.isRequired,
	poster: PropTypes.string.isRequired,
	genres: PropTypes.arrayOf(PropTypes.string).isRequired
};

export default Movie;